body {
  font-family: Arial;
}

.layout {
  max-width: 960px;
  margin: 0 auto;
}

.pointer {
  width: 300px;
  margin: 20px 0 0 20px;
}

.pointer-fluid {
  margin: 20px 0 0 20%;
  width: 30%;
}

.pointer__instructions, .pointer-fluid__instructions {
  padding-bottom: 20px;
  font-size: larger;
  text-align: center;
}

.touch {
  width: 300px;
  margin: 20px auto;
}

.touch-fluid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.touch-fluid__image-container {
  -ms-flex: 0 0 100%;
      flex: 0 0 100%;
  width: 80%;
  margin: 20px auto;
}

.touch-fluid__instructions {
  -ms-flex: 0 0 100%;
      flex: 0 0 100%;
  width: 80%;
  margin: 0 auto;
}

@media (min-width: 480px) {
  .touch-fluid {
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .touch-fluid__image-container{
    -ms-flex: 0 0 30%;
        flex: 0 0 30%;
    width: auto;
  }

  .touch-fluid__instructions {
    -ms-flex: 0 0 50%;
        flex: 0 0 50%;
    width: auto;
    padding-top: 20px;
  }
}



